.App-container{
    .insta-container{
        display: flex;
        flex-wrap: wrap;
        max-width: 900px;
        margin: auto;
        .insta-pic{
            width: 300px;
            height: 300px;
            padding: 10px;
            &>img{
                width: 100%;
                height: 100%;
                background-color: #ccc;
            }
        }
        @media (max-width: 700px) {
            .insta-pic{
                width: 50%;
                height: 400px;
            }
        }
        @media (max-width: 400px) {
            .insta-pic{
                width: 100%;
                height: 400px;
            }
        }
    }
}