.App-container{
    $clock_height: 300px;
    $clock_width: $clock_height;
    $second_hand_height: 4px;
    $second_hand_color: red;
    $minute_hand_height: 8px;
    $minute_hand_color: blue;
    $hour_hand_height: 12px;
    $hour_hand_color: yellow;
    .clock-container{
        width: $clock_height;
        height: $clock_height;
        background-color: #ccc;
        border-radius: 50%;
        position: relative;
        .clock-minute-mark-container{
            position: relative;
            width: $clock_height;
            height: $clock_height;
            transform: rotate(-90deg);
            overflow: hidden;
            .clock-minute-marker{
                color: transparent;
                overflow: hidden;
                height: 2px;
                width: 100%;
                top: #{$clock_height/2};
                display: flex;
                justify-content: flex-end;
                position: absolute;
                &:after{
                    content: "";
                    background-color: #000;
                    height: 2px;
                    width: 10px;
                }
                &:nth-child(5n){
                    height: 4px;
                    &:after{
                        height: 4px;
                    }
                }
            }
        }
        .clock-minute-hand{
            width: 100%;
            height: $minute_hand_height;
            top: calc(50% - #{$minute_hand_height/2} );
            position: absolute;
            &:after{
                content: "";
                background-color: $minute_hand_color;
                position: absolute;
                width: 40%;
                left: calc(50% + #{$minute_hand_height/4} );
                height: 100%;
                transform: rotate(-90deg);
                transform-origin: 0 center;
            }
        }
        .clock-second-hand{
            width: 100%;
            height: $second_hand_height;
            top: calc(50% - #{$second_hand_height/2} );
            position: absolute;
            &:after{
                content: "";
                background-color: $second_hand_color;
                position: absolute;
                width: 45%;
                left: calc(50% + #{$second_hand_height/2} );
                height: 100%;
                transform: rotate(-90deg);
                transform-origin: 0 center;
            }
        }
        .clock-hour-hand{
            width: 100%;
            height: $hour_hand_height;
            top: calc(50% - #{$hour_hand_height/2} );
            position: absolute;
            &:after{
                content: "";
                background-color: $hour_hand_color;
                position: absolute;
                width: 30%;
                left: calc(50% + #{$hour_hand_height/6} );
                height: 100%;
                transform: rotate(-90deg);
                transform-origin: 0 center;
            }
        }
    }
}